@use '../../../colors.scss' as col;
@use '../../../common.scss';

.find-inner {
	display: flex;
	flex-direction: column;
	flex: 1 1 auto;
	overflow: auto;
}

.find-controls {
	padding: 0.25em 0.5em;
}

.form-row, .form-label {
	display: flex;
	align-items: center;

	& > * {
		margin-right: 0.5em;

		&:last-child {
			margin-right: 0;
		}
	}
}

.found-messages {
	display: flex;
	overflow: auto;
	flex: 0 1 auto;
	flex-direction: column;
}

.paginator {
	display: flex;
	align-items: center;
	justify-content: center;
	padding: 1em 0;
}

.page-button {
	@include common.icon-button;
	width: 24px;
	height: 24px;
}

.prev {
	background-image: url('icon-prev.svg');
}

.next {
	background-image: url('icon-next.svg');
}